import React from 'react';
import { useDispatch } from 'react-redux';
import './PizzasProducts.css';
import { ColdDrinksData } from './ProductsData';
import { HotDrinksData } from './ProductsData';
import { addProduct } from '../Actions';

function Content() {
    const dispatch = useDispatch();

    return (
        <div className='products-container' id='drinks'>
            <h1>Cold drinks:</h1>
            <div className='products-space'>
                {ColdDrinksData.map((item, index) => (
                    <div className='product-card' key={index}>
                        <img src={item.img} alt={item.alt} />
                        <div className='product-info'>
                            <h2 className='product-name-drinks'>{item.name}</h2>
                            <h3 className='product-price'>${item.price}</h3>
                            <button
                                onClick={() =>
                                    dispatch(
                                        addProduct({
                                            name: item.name,
                                            price: item.price,
                                            index: item.index,
                                        })
                                    )
                                }
                            >
                                Add to Cart
                            </button>
                        </div>
                    </div>
                ))}
            </div>

            <h1>Hot drinks:</h1>
            <div className='products-space'>
                {HotDrinksData.map((item, index) => (
                    <div className='product-card' key={index}>
                        <img src={item.img} alt={item.alt} />
                        <div className='product-info'>
                            <h2 className='product-name-drinks'>{item.name}</h2>
                            <h3 className='product-price'>${item.price}</h3>
                            <button
                                onClick={() =>
                                    dispatch(
                                        addProduct({
                                            name: item.name,
                                            price: item.price,
                                            index: item.index,
                                        })
                                    )
                                }
                            >
                                Add to Cart
                            </button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default Content;
